<template>
    <b-spinner
        :label="items.label"
        :spinnerTitle="items.spinnerTitle"
        :spinnerSubtitle="items.spinnerSubtitle"
        :max="items.max"
        >
    </b-spinner>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
