<template>
    <b-field :class="dropdown" style="margin-bottom: 0px; margin: 0.25em;">
        <p class="control">
            <b-dropdown
                :position="items.dropdownPosition">
                <button 
                    class="button" 
                    :class="button"
                    slot="trigger">
                    <b-icon 
                        v-if="items.buttonIconLeft"
                        :icon="items.buttonIconLeft"
                        :size="items.buttonSize">
                    </b-icon>
                    <span>
                    {{ items.buttonCount }} {{ items.buttonTitle }} {{ items.buttonSubtitle }}
                    </span>
                    <b-icon 
                        v-if="items.buttonIconRight"
                        :icon="items.buttonIconRight"
                        :size="items.buttonSize">
                    </b-icon>
                </button>
                <b-dropdown-item 
                    v-if="items.dropdownTitle || items.cardTitle"
                    :custom="items.dropdownCustom"
                    :paddingless="items.dropdownPaddingless">
                    <div v-if="items.cardTitle">
                        <s-card :opts="items" :parentOpts="parentOpts"></s-card>
                    </div>
                    <span v-else>{{ items.dropdownTitle }}</span>
                </b-dropdown-item>
            </b-dropdown>
        </p>
        <p v-if="items.dropdownCount" class="control">
            <button class="button is-primary">
                {{ items.dropdownCount }}
            </button>
        </p>
    </b-field>
</template>

<script>
    export default {
        props: ['opts', 'parentOpts'],
        data() {
            return {
                items: {},
                data: null
            }
        },
        computed: {
            dropdown() {
                return [
                    this.items.colour || null
                ];
            },
            button() {
                return [
                    this.items.buttonColour || null
                ];
            },
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>

<style>
.dropdown-content {
    padding-bottom:0px !important;
    padding-top:0px !important;
}
.modal-card-body div.dropdown-menu {
    display: none !important;
}
</style>

