<template>
    <div>
        <b-field :label="label"></b-field>
        <div class="control level" :class="rootClasses">
            <div v-if="spinnerTitle">
                <label><strong>{{ spinnerTitle }}</strong></label>
                <div v-if="spinnerSubtitle">
                    <label>{{ spinnerSubtitle }}</label>
                </div>
            </div>
            <button v-if="!mobileNative" class="button spinner" @click.prevent="onDecrement" :disabled="decActive">
                <b-icon icon="minus"></b-icon>
            </button>
            <input
                ref="input"
                :min="min"
                :max="max"
                :readonly="readonly"
                class="input spinnerInput"
                :class="[inputClasses, customClass]"
                type="number"
                :step="step"
                :value="computedValue"
                v-bind="$attrs"
                @input="onInput">
            <div>
                <button v-if="!mobileNative" class="button spinner"  @click.prevent="onIncrement">
                    <b-icon icon="plus"></b-icon>
                </button>
            </div>
            <span v-if="validity" class="validity"></span>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'BSpinner',
        inheritAttrs: false,
        props: {
            value: [Number],
            type: {
                type: String,
                default: 'text'
            },
            buttonsRounded: {
                type: Boolean,
                default: true
            },
            step: {
                type: Number,
                default: 1
            },
            min: {
                type: Number,
                default: 0
            },
            max: {
                type: Number,
                default: 999
            },
            customClass: {
                type: String,
                default: ''
            },
            label: {
                type: String,
                default: ''
            },
            spinnerSubtitle: {
                type: String,
                default: ''
            },
            spinnerTitle: {
                type: String,
                default: ''
            },
            validity: {
                type: Boolean,
                default: true
            },
            readonly: {
                type: Boolean,
                default: false
            },
            mobileNative: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                newValue: this.value
            }
        },
        computed: {
            computedValue: {
                get() {
                    return this.newValue
                },
                set(value) {
                    this.newValue = value
                    this.$emit('input', value)
                }
            },
            rootClasses() {
                return [
                    this.iconPosition,
                    this.size,
                    {
                        'is-expanded': this.expanded,
                        'is-loading': this.loading,
                        'is-clearfix': !this.hasMessage,
                        'is-rounded': this.buttonsRounded
                    }
                ]
            },
            inputClasses() {
                return [
                    this.statusType,
                    this.size,
                    { 'is-rounded': this.rounded }
                ]
            },
            decActive() {
                return this.newValue == this.min;
            },
            incActive() {
                return this.newValue == this.max;
            },
            /**
             * Check if have any message prop from parent if it's a Field.
             */
            hasMessage() {
                return !!this.statusMessage;
            }
        },
        watch: {
            /**
             * When v-model is changed:
             *   1. Set internal value.
             */
            value(value) {
                this.newValue = value;
            }
        },
        methods: {
            /**
             * Input's 'input' event listener, 'nextTick' is used to prevent event firing
             * before ui update, helps when using masks (Cleavejs and potentially others).
             */
            onInput(event) {
                this.$nextTick(() => {
                    if (event.target) {
                        this.computedValue = event.target.value;
                    }
                })
            },
            onIncrement() {
                let newValue = parseInt(this.newValue) + this.step;
                if (newValue <= this.max) {
                    this.newValue = newValue;
                }
            },
            onDecrement() {
                let newValue = parseInt(this.newValue) - this.step;
                if (newValue >= this.min) {
                    this.newValue = newValue;
                }
            }
        },
        beforeMount: function () {
            typeof this.min == "number" ? this.newValue = parseInt(this.min) : 0;
        }
    }
</script>

<style>
.control.is-rounded .spinner {
    border-radius: 290486px;
}
.spinnerInput {
    width: 4em;
}
input:invalid~.validity:after {
    color: #ff3860;
    font: normal normal normal 24px/1 "Material Design Icons";
    content: "\F028";
    padding-left: 5px;
}

input:valid~.validity:after {
    color: #23d160;
    font: normal normal normal 24px/1 "Material Design Icons";
    content: "\F12C";
    padding-left: 5px;
}
</style>
