<template>
    <nav class="navbar" :class="navBar" role="navigation" aria-label="main navigation">
        <div class="navbar-brand">
            <a class="navbar-item" :class="settings.navItemLogo" :href="settings.siteUrl">
                <span :class="navTitle" v-if="!items.hideSiteName">{{ this.items.siteName ? this.items.siteName : settings.siteName }}</span>
                <div v-else-if="items.siteLogoSVG" v-html="items.siteLogoSVG" style="display: flex;flex-direction: column;justify-content: center;"></div>
                <img v-else :src="items.siteLogo" width="112" height="28">
            </a>
            <s-component
                v-for="(item, key) in settings.navbarIcons" :key="key"
                :index="key"
                :is="item.type"
                :opts="item">
            </s-component>
            <a role="button" class="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample" :class="{ 'is-active': active }" @click.prevent="active = !active">
                <span aria-hidden="true"></span>
                <span aria-hidden="true"></span>
                <span aria-hidden="true"></span>
            </a>
        </div>
        <div id="navbarBasicExample" class="navbar-menu" :class="[{ 'is-active': active }]">
            <div v-if="opts && opts.blocks" class="navbar-start">
                <s-navitem :item="item" v-for="(item, key) in opts.blocks" :key="key" :index="key" :settings="settings"></s-navitem>
            </div>
            <div v-else class="navbar-start">
                <s-navitem :item="item" v-for="(item, key) in opts" :key="key" :index="key" :settings="settings"></s-navitem>
            </div>
            <div class="navbar-end">
                <div class="navbar-item" v-for="(item, key) in settings.navbarEnd" :key="key">
                    <s-component
                        :index="key"
                        :is="item.type"
                        :opts="item">
                    </s-component>
                </div>
            </div>
        </div>
    </nav>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                items: {},
                active: false
            }
        },
        computed: {
            navBar() {
                return [
                    this.items.navColour || this.settings.navColour, 
                    {'is-transparent': this.settings.navTransparent}, 
                    {'is-fixed-top': this.settings.navFixedTop}, 
                    {'is-fixed-bottom': this.settings.navFixedBottom}
                ]
            },
            navTitle() {
                return [
                    this.settings.navFont,
                    this.settings.navFontSize
                ];
            }
        },
        beforeMount: function () {
            if (this.opts !== null) {
                this.items = this.opts;
            }
        }
    }
</script>

<style scoped>
.racing {
    font-family: 'Racing Sans One', cursive;
    color: red;
}
.mukta {
    font-family: 'Mukta Mahee', sans-serif;
}
.is-transparent {
    background-color: transparent !important;
}
</style>

