<template>
    <div>
        <b-field label="MORE OPTIONS">
            <b-switch v-model="isOpen"></b-switch>
        </b-field>
        <b-collapse :open.sync="isOpen">
            <s-component
                v-for="(item, index) in parentOpts['collapse']" 
                :key="index + 'collapse'"
                :is="item.type"
                :opts="item">
            </s-component>
        </b-collapse>
    </div>
</template>

<script>
    export default {
        props: ['opts', 'parentOpts'],
        data() {
            return {
                items: {},
                isOpen: false
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>